import React, { useEffect } from "react";

import { ErrorMessage } from "components";
import { withLinkToSearchPageHOC } from "hocs";
import { useNavigate } from "react-router-dom";

export const NotFoundPage = () => {
    const navigate = useNavigate();
    useEffect(() => {
        setTimeout(() => {
            navigate(-1);
        }, 3000);
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);
    return <ErrorMessage errorMessage={`Strona o adresie ${decodeURIComponent(window.location.href)} nie istnieje`} />;
};

export default React.memo(withLinkToSearchPageHOC(NotFoundPage));
